html {
	scroll-behavior: smooth;
}

body {
	margin: 0;
	padding: 0;
	font-family: 'Open Sans', sans-serif;
	font-size: 16px;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

* {
	box-sizing: border-box;
}

/* --- Other basic rules --- */

h1, h2, h3, h4, h5, h6 {
	margin: .75em auto;
	font-weight: 700;
	letter-spacing: .0625em;
	text-align: center;
}

h1.\--normal, h2.\--normal, h3.\--normal, h4.\--normal, h5.\--normal, h6.\--normal {
	color: #333;
}

h1.\--inverted, h2.\--inverted, h3.\--inverted, h4.\--inverted, h5.\--inverted, h6.\--inverted {
	color: #fff;
}

h1 { font-size: 3em; } h2 { font-size: 2.5em; } h3 { font-size: 2em; } h4 {font-size: 1.75em;} h5 { font-size: 1.5em; } h6 { font-size: 1.25em; }

p {
	font-size: 1em;
	line-height: 150%;
}

p.\--normal {
	color: #555;
}

p.\--inverted {
	color: #fff;
}

a {
	text-decoration: none;
}

a.\--normal, a.\--normal:visited {
	color: #ad87ff;
}

a.\--inverted, a.\--inverted:visited {
	color: #ffd787;
}

a.\--normal:hover, a.\--normal:active {
	color: #7F53DC;
}
a.\--inverted:hover, a.\--inverted:active {
	color: #e6b34c;
}

a.\--normal:hover, a.\--inverted:hover {
	transition: color .25s ease-in;
}

.section {
	width: 100%;
	padding: 2em 0;
	display: block;
}

.section--white {
	background: #fff;
}

.section--purple {
	background: #AD87FF;
}

/* --- Styling for the button component. --- */

.button {
	display: block;
	padding: .75em;
	letter-spacing: .05em;
	border-style: solid;
	border-width: .125em;
	text-align: center;
	width: 100%;
}

a.button--normal, a.button--normal:visited {
	color: #333;
	border-color: #333;
}

a.button--inverted, a.button--inverted:visited {
	color: #eee;
	border-color: #eee;
}

a.button--normal:active {
	border-color: #7F53DC;
	background: #7F53DC;
	color: #fff;
	transition: all .125s ease-in;
}

a.button--inverted:active {
	border-color: #fff;
	background: #fff;
	color: #7F53DC;
	transition: all .125s ease-in;
}

@media screen and (min-width: 768px) {

.button {
	display: inline-block;
	width: auto;
	margin: auto;
}

a.button--normal:hover {
	border-color: #7F53DC;
	color: #7F53DC;
	transition: all .25s ease-in;
}

a.button--inverted:hover {
	border-color: #ffd787;
	color: #ffd787;
	transition: all .25s ease-in;
}

a.button--normal:active {
	border-color: #7F53DC;
	background: #7F53DC;
	color: #fff;
	transition: none;
}

a.button--inverted:active {
	border-color: #e6b34c;
	background: #e6b34c;
	color: #7F53DC;
	transition: none;
}

}
/* --- Contact Section Styles --- */

.contact {
	display: block;
	margin: auto;
	width: 90%;
	text-align: center;
}

.contact__anchor-tag {
	font-weight: 700;
}

@media screen and (min-width: 768px) {

.contact {
	width: 768px;
}

}

/* --- Social Icon --- */

.icon-container {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-wrap: wrap;
	        flex-wrap: wrap;
	-webkit-align-items: center;
	        align-items: center;
}

a.social-icon, a.social-icon:visited {
	color: #ddd;
}

.social-icon {
	-webkit-flex: auto;
	        flex: auto;
	margin: 1.5em;
}

.social-icon__img {
	display: block;
	text-align: center;
	font-size: 2.5em;
	margin: auto;
	display: block;
}

.social-icon__label {
	display: none;
}

@media screen and (min-width: 768px) {

a.social-icon {
	cursor: pointer;
}

a.social-icon:hover {
	color: #ffd787;
	transition: .25s ease-in;
}

a.social-icon:active {
	color: #e6b34c;
}

.social-icon__label {
	font-size: .75em;
	text-align: center;
	display: block;
}

}
/* --- Styling for the Event component --- */

a.event {
	/* For overriding default anchor tag styling */
	color: #333;
	text-decoration: none;
}

.event {
	display: block;
	padding: 1em;
}

.event__title {
	display: block;
	font-weight: 600;
	font-size: 1.5em;
	text-align: center;
}

.event__time {
	display: block;
	text-align: center;
}

.event__day, .event__time-range {
	display: inline-block;
	font-size: .75em;
	color: #666;
}

.event__day::after {
	content: " | ";
}

a.event:hover .event__title, a.event:hover .event__day, a.event:hover .event__time-range {
	color: #AD87FF;
	transition: .25s ease-in;
}

a.event:active .event__title, a.event:active .event__day, a.event:active .event__time-range {
	color: #7F53DC;
}

@media screen and (min-width: 768px) {
/* --- Non-mobile styles --- */

.event {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	        flex-direction: row;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
	margin: .25em;
}

.event__title {
	-webkit-flex: 4 1 auto;
	        flex: 4 1 auto;
	text-align: left;
}

.event__time {
	-webkit-flex: 1 1 auto;
	        flex: 1 1 auto;
	text-align: right;
}

.event__day {
	display: block;
	font-size: 1.25em;
	color: #333;
	padding: 0 0 .75em 0;
}

.event__time-range {
	display: block;
}

.event__day::after {
	content: "";
}

}

/* --- EventList styles --- */

.schedule {
	display: block;
	width: 90%;
	text-align: center;
	margin: auto;
}

.events {
	margin: 1em auto;
}

@media screen and (min-width: 768px) {
/* --- Non-mobile styles --- */

.schedule {
	width: 48em;
}

}
/* --- Navbar styles  --- */

.navbar {
	display: block;
	height: 3.5em;
	position: fixed;
	width: 100%;
	top: 0;
	left: 0;
	z-index: 10;
}

/* --- Hamburger Menu Button --- */

.hamburger-menu {
	display: inline-block;
	position: absolute;
	z-index: 11;
	width: 2.25em;
	top: 1em;
	left: 1em;
	cursor: pointer;
}

.hamburger-menu__bar {
	display: block;
	height: .25em;
	margin-bottom: .375em;
	background: #333;
	transition: margin .25s ease-in, background .25s ease-in;
}

.hamburger-menu--menuExpanded .hamburger-menu__bar {
	background: #fff;
	margin: 0 .125em .375em;
}

@media screen and (min-width: 768px) {

.hamburger-menu {
	display: none;
}

}

/* --- Navigation Items --- */

.nav-list {
	display: block;
	padding: 3.5em 0 0 0;
	background: #6934db;
	-webkit-transform: translateY(-100%);
	        transform: translateY(-100%);
	opacity: 0;
	transition: opacity .25s ease-in, -webkit-transform .25s ease-in;
	transition: transform .25s ease-in, opacity .25s ease-in;
	transition: transform .25s ease-in, opacity .25s ease-in, -webkit-transform .25s ease-in;
	box-shadow: 0px 1px 2px #777;
}

.nav-list--expanded {
	-webkit-transform: translateY(0%);
	        transform: translateY(0%);
	opacity: 1;
}

.nav-list a {
	color: #fff;
	text-decoration: none;
}

.nav-list__item {
	display: block;
	text-align: center;
	padding: .75em 0;
	font-size: 1.25em;
	font-weight: 600;
	background: #7F53DC;
	letter-spacing: .0625em;
}

.nav-list__item:active {
	background: #6934db;
	color: #eee;
}

@media screen and (min-width: 768px) {

.nav-list, .nav-list--expanded {
	padding:  1em .5em .5em;
	background: none;
	-webkit-transform: none;
	        transform: none;
	opacity: 1;
	text-align: center;
	transition: none;
	box-shadow: none;
}

.nav-list a {
	color: #333;
}

.nav-list__item {
	display: inline-block;
	padding: .25em 1em;
	font-size: 1.125em;
	font-weight: 400;
	background: none;
}

.nav-list__item:hover {
	color: #7F53DC;
	transition: color .25s;
}

.nav-list__item:active {
	color: #6934db;
	background: none;
	transition: color .25s;
}

}

/* --- Footer styles --- */

.footer {
	width: 100%;
	text-align: center;
	background: #7F53DC;
	padding: 1em 0;
	letter-spacing: .0625em;
}

.footer__primary-txt {
	font-size: 1em;
	font-weight: 700;
	color: #fff;
	margin: .25em auto .5em;
}

.footer__secondary-txt {
	font-size: .75em;
	font-weight: 400;
	color: #ddd;
}
/* --- About Section Styles --- */

.about {
	width: 90%;
	display: -webkit-flex;
	display: flex;
	margin: auto;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-align-content: center;
	        align-content: center;
}

.about__content-con {
	padding: 1em;
	text-align: center;
}

.about__img {
	max-width: 100%;
	margin-right: 2em;
	display: inline-block;
}

@media screen and (min-width: 768px) {

	.about {
		width: 768px;
		-webkit-flex-direction: row;
		        flex-direction: row;
	}

	.about__content-con--ralign, .about__content-con--ralign h3 {
		text-align: right;
	}
	
	.about__content-con--lalign, .about__content-con--lalign h3 {
		text-align: left;
	}
}
/* --- Front Styles --- */

.front {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-justify-content: center;
	        justify-content: center;
	width: 90%;
	margin: .5em auto 0;
	padding: .5em;
	-webkit-align-items: center;
	        align-items: center;
}

.front__logo {
	width: 100%;
	margin-right: 1em;
}

.front__tagline {
	margin-left: 1em;
	text-align: center;
}

.front__bar {
	display: inline-block;
	margin: .5em 0;
	width: 8em;
	height: .375em;
	background: #AD87FF;
}

@media screen and (min-width: 768px) {

.front {
	-webkit-flex-direction: row;
	        flex-direction: row;
}

.front__tagline, .front__tagline h1 {
	text-align: left;
	max-width: 23em;
}

.front__logo {
	padding: 0 2em;
	max-width: 30em;
}

}

@media screen and (min-width: 1024px;) {

.front {
	width: 1024px;
}

}
